@import '_variables';

@mixin root-selector($new-class) {
	$root-selector: html;
	$current-selector: &;
	$new-selector: ();

	@each $item in $current-selector {
		$first-item: nth($item, 1);

		$appended-item: $root-selector + $new-class + ' ' + $first-item;

		$new-item: set-nth($item, 1, $appended-item);
		$new-selector: append($new-item, $new-selector);
	}

	@at-root #{$new-selector} {
		@content;
	}
}


// 빈 오브젝트
@mixin white-space {
	background-color: rgba(255,0,0,0.125);
}

@mixin material-icons($style: 'Material Icons', $type: 0, $weight: 400, $size: 24rem, $color: &) {
	font-family: $style;
	font-weight: normal;
	font-style: normal;
	font-size:  $size;
	line-height: 1;

	@if $color != & {
		color: $color;
	}

	text-transform: none;
	letter-spacing: normal;
	word-wrap: normal;
	white-space: nowrap;
	direction: ltr;
	text-decoration: none;
	// Support for all WebKit browsers.
	-webkit-font-smoothing: antialiased;
	// Support for Safari and Chrome.
	text-rendering: optimizeLegibility;
	// Support for Firefox.
	-moz-osx-font-smoothing: grayscale;
	// Support for IE.
	font-feature-settings: 'liga';
}

@mixin material-symbols-outlined($style: 'Material Symbols Outlined', $type: 0, $weight: 400, $size: 24rem, $color: #000) {
	font-family: $style;
	font-weight: normal;
	font-style: normal;
	font-size: $size;
	line-height: 1;
	color: $color;
	letter-spacing: normal;
	text-transform: none;
	display: inline-block;
	white-space: nowrap;
	word-wrap: normal;
	direction: ltr;
	-webkit-font-feature-settings: 'liga';
	-webkit-font-smoothing: antialiased;

	font-variation-settings: 'FILL' 0, 'wght' 400, 'GRAD' 0, 'opsz' 48;
}

@mixin font-awesome {
	display: inline-block;
	vertical-align: top;
	width: 1.5em;
	height: 1.5em;
	line-height: 1.5;
	font-size: inherit;
	// font-family: FontAwesome;
    font-family: "Font Awesome 5 Pro";
	font-weight: 900;
	text-align: center;
	
	text-rendering: auto;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

@mixin clear {
	content: '';
	display: block;
	clear: both;
}

@mixin hidden {
	display: block;
	position: absolute;
	overflow: hidden;
	visibility: hidden;
	margin: -1px;
	padding: 0;
	width: 1px;
	height: 1px;
	line-height: 1px;
	font-size: 1px;
	border: 0;
	clip: rect(0 0 0 0);
	clip: rect(0,0,0,0);
	pointer-events: none;
}

@mixin image-replace {
	opacity: 0;
	animation-name: text-fadein;
	animation-duration: 1s;
	animation-delay: 1s;
	animation-fill-mode: forwards;
}

@keyframes text-fadein {
	0% {opacity: 0;}
	100% {opacity: 1;}
}


// @inlcude ellipsis(1);
// @inlcude ellipsis(3);
@mixin ellipsis($line-clamp) {
	display: block;
	display: -webkit-box;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: $line-clamp;
	text-overflow: ellipsis;
	max-height: 1.5em * $line-clamp;
	word-wrap: break-word;
	overflow: hidden;
}


